.calculator{
  width: 30rem;
  height: 40rem;
  border: 3px solid black;
  margin: auto;
  position: relative;
  top: 8rem;
  border-radius: 1rem;
  overflow: hidden;
  background-color: #292D2A;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 1.5fr repeat(5, 1fr);
}

#display {
  grid-column: span 4; 
  font-size: 4rem;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  padding: 2%;
  text-align: end;
}

button:nth-child(18) {
  grid-column: span 2; 
}

button {
  border: 0.5px solid #292D2A;
  background-color: #6C6C6C;
  font-size: 1.9rem;
  color: inherit;
  transition: all 300ms;
  border-radius: 2px;
}

button:hover, button:focus, button:active {
  background-color: #787878;
}

button:focus {
  outline:none;
}

.operator {
  background-color: #FFA01C;
  font-size: 3rem;
}

.darker {
  background-color: #4C4D4C;
}

.darker:hover, .darker:focus, .darker:active {
  background-color: rgb(108, 108, 108)
}

.operator:hover, .operator:focus, .operator:active {
  background-color: white;
  color:#FFA01C;
}

@media (max-width: 480px) {
  .calculator {
    width: auto;
    max-height: 100svh;
    position: static;
    border: none;
    border-radius: 0;
    background-color: black;
    grid-template-rows: 3fr repeat(5, 1fr);
    padding: 2vh 5vw;
    gap: 0.75rem;
    justify-content: center;
    max-width: 380px;
    margin: auto;
  }

  button {
    border-radius: 50%;
    border: none;
    background-color: #323232;
    height: 4.5rem;
    width: 4.5rem;
    max-width: 4.5rem;
    max-height: 4.5rem;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
  }

  button:hover, button:focus, button:active {
    background-color: #737373;
  }

  .darker {
    background-color: #A5A5A5;
    color:#323232;
  }
  
  .darker:hover, .darker:focus, .darker:active {
    background-color: #D9D9D9;
  }

  #zero {
    width: 10rem;
    max-width: 10rem;
    border-radius: 2.5rem;
    justify-content: start;
    padding-left: 2rem;
  }

  .operator {
    max-width: 4.5rem;
    padding: 0;
  }
}
